@media(max-width: 991px) {
    .header{
        &-container {
            justify-content: center;
        }
    }
    .header-right{
        display: none;
    }
    .header-logo__img{
        justify-content: center;
    }
    .intro{

        &__info{

            &-container{
                display: flex;
                justify-content: center;
                
            }
        }
        &-container {
            .row{
                gap: 40px;
            }
        }
        &-title{
            text-align: center;
        }
        &__baner{

            &-img{
                width: 100%;
                height: 100%;
                object-fit: contain;
            }
            margin: 0;
        }
        &-descr{
            text-align: center;
        }
    }
    .intro__banner-img{
        width: 100%;
        height: 100%;
        object-fit: contain;
    }
    .contacts__item-up{
        flex-direction: row;
        gap: 0;
    }
    .contacts__item-address{
        text-align: initial;
    }
    .contacts__item-num{
        display: inline-block;
    }
}